import useSound from "use-sound";

import Card from "./Deck/Card";
import { mazzo, retro } from "./Deck/cards";
import { mischia } from "./script/mischia";
import "./tavolo.css";

import suonoScarto from "../Sound/scarta.mp3";
import dailecarte from "../Sound/dailecarte.mp3";
import pesca from "../Sound/pesca.mp3";
import scopa from "../Sound/scopa.mp3";
