const getItemStyle2 = (isDragging, draggableStyle) => ({
  // some basic styles to make the items look a bit nicer
  userSelect: "none",
  //padding: grid * 2,
  //margin: `0 ${grid}px 0 0`,

  // change background colour if dragging
  background: isDragging ? "" : "",

  // styles we need to apply on draggables
  ...draggableStyle,
});
const getItemStyle3 = (isDragging, draggableStyle) => ({
  // some basic styles to make the items look a bit nicer
  userSelect: "none",
  //padding: grid * 2,
  //margin: `0 ${grid}px 0 0`,

  // change background colour if dragging
  background: isDragging ? "" : "",

  // styles we need to apply on draggables
  ...draggableStyle,
});
const reorder = (list, startIndex, endIndex) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};
const getListStyle = (isDraggingOver) => ({
  display: "flex",
  position: "relative",
  padding: "12px 6px",
  overflow: "hidden",
  width: "274px",
  margin: "auto",
  borderRadius: "10px",
  top: 10,
  position: "absolute",
  left: "calc(50% - 137px)",
});
const getListStyle2 = (isDraggingOver) => ({
  //background: isDraggingOver ? "rgba(255,255,255,.2)" : "",
  display: "flex",
  padding: "12px 6px",
  overflow: "hidden",
  width: "274px",
  margin: "auto",
  borderRadius: "10px",
  bottom: 10,
  position: "absolute",
  left: "calc(50% - 137px)",
});
const getListStyle3 = (isDraggingOver) => ({
  background: isDraggingOver ? "rgba(255,255,255,.2)" : "",
  display: "flex",
  padding: "12px 6px",
  overflow: "hidden",
  height: "126px",
  //margin: "auto",
  borderRadius: "10px",
  bottom: 10,
  textAlign: "center",
  minWidth: 200,
});
function Tavolo(props) {
  const [cards, setCards] = useState([]);
  const [playerOne, setPlayerOne] = useState([]);
  const [playerTwo, setPlayerTwo] = useState([]);
  const [tavolo, setTavolo] = useState([]);
  const [sommata, sommamele] = useState([]);

  const [itStart, setStarting] = useState(true);
  const [inMatch, setInMatch] = useState(false);

  const [cartePrese1, prendiCarte1] = useState([]);
  const [cartePrese2, prendiCarte2] = useState([]);
  const [scope1, settaScope1] = useState([]);
  const [scope2, settaScope2] = useState([]);

  const [ultimaPresa, setUltimaPresa] = useState(0);

  const [settebello, setTebello] = useState(0);
  const [settanta, setTanta] = useState(0);
  const [denari, setDenari] = useState(0);

  const [playScarto] = useSound(suonoScarto);
  const [playDailecarte] = useSound(dailecarte);
  const [playPesca] = useSound(pesca);
  const [playScopa] = useSound(scopa);
  const [playSettebello] = useSound(bello);
  const [playbackgroundMusic] = useSound(backgroundMusic, {
    volume: 0.1,
  });

  useEffect(() => {
    setCards(mischia(mazzo));
  }, []);

  function finePartita() {
    if (tavolo.length > 0) {
      incartata();
      //finePartita();
    } else {
      let punti1 = 0;
      let punti2 = 0;
      let cartePunto = 0;

      if (cartePrese1.length > cartePrese2.length) {
        punti1 = punti1 + 1;
        cartePunto = 1;
      } else {
        punti2 = punti2 + 1;
        cartePunto = 2;
      }
      switch (settebello) {
        case 1:
          punti1 = punti1 + 1;
          break;
        case 2:
          punti2 = punti2 + 1;
          break;
        default:
          console.log("IL SETTEBBELLO  SPARITO!");
      }
      switch (settanta) {
        case 1:
          punti1 = punti1 + 1;
          break;
        case 2:
          punti2 = punti2 + 1;
          break;
        default:
          console.log("LA SETTANTA PATTA");
      }
      switch (denari) {
        case 1:
          punti1 = punti1 + 1;
          break;
        case 2:
          punti2 = punti2 + 1;
          break;
        default:
          console.log("I DENARI PATTANO!");
      }

      let giocatore1 = {
        stato: punti1 > punti2 ? true : false,
        punti: punti1,
        settebello: settebello === 1,
        settanta: settanta === 1,
        denari: denari === 1,
        carte: cartePunto === 1,
        scope: scope1.length,
      };
      let giocatore2 = {
        stato: punti2 > punti1 ? true : false,
        punti: punti2,
        settebello: settebello === 2,
        settanta: settanta === 2,
        denari: denari === 2,
        carte: cartePunto === 2,
        scope: scope2.length,
      };
      const vincitore = punti1 > punti2 ? "1" : "2";
      alert("Vince il player: " + vincitore);
      let giocatori = [giocatore1, giocatore2];

      console.log(giocatori);
    }
  }

  function daiLeCarte() {
    //console.log("dai le carte: " + inMatch);

    let carte = cards;

    if (carte.length > 0) {
      if (!inMatch) {
        setPlayerOne(carte.splice(0, 3));
        setPlayerTwo(carte.splice(0, 3));
        setInMatch(true);
        playDailecarte();
      }
      if (itStart) {
        setTavolo(carte.splice(0, 4));
        setStarting(false);
        playbackgroundMusic();
      }
      setCards(carte);
    } else {
      finePartita();
    }
  }
  let numero = [];
  let sommerella = 0;

  function somma(crd, deseleziona, player) {
    console.log(player);
    numero = sommata;

    if (deseleziona) {
      // SOTTRAZIONE
      for (let i = 0; i < numero.length; i++) {
        if (numero[i].id === crd.id) {
          numero.splice(i, 1);
        }
      }
      sommamele(numero);
    } else {
      // SOMMA
      numero.push(crd);
      sommamele(numero);
    }

    sommerella = 0;
    // SOMMA DEL VALORE DELLE CARTE SELEZIONATE
    for (let i = 0; i < sommata.length; i++) {
      sommerella += sommata[i].valore;
    }

    if (sommerella === 15) {
      playPesca();
      playScarto();
      switch (player) {
        case 1:
          let crtprs1 = cartePrese1;
          for (let i = 0; i < sommata.length; i++) {
            crtprs1.push(sommata[i]);
          }

          prendiCarte1(crtprs1);
          incartata(player);
          incartata();

          break;
        case 2:
          let crtprs2 = cartePrese2;
          for (let i = 0; i < sommata.length; i++) {
            crtprs2.push(sommata[i]);
          }
          prendiCarte2(crtprs2);
          incartata(player);
          incartata();

          break;
        default:
          console.log("Nessuno prende");
      }

      sommamele([]);

      let plone = playerOne;
      let pltwo = playerTwo;
      let tavolicchio = [];
      if (tavolo.length > 0) {
        for (let i = 0; i < tavolo.length; i++) {
          tavolicchio.push(tavolo[i]);
        }
      }

      switch (player) {
        case 1:
          let numeroSette1 = 0;
          let numeroDenari1 = 0;
          for (let i = 0; i < cartePrese1.length; i++) {
            for (let j = 0; j < playerOne.length; j++) {
              if (playerOne[j].id === cartePrese1[i].id) {
                plone.splice(j, 1);
              }
            }
            for (let j = 0; j < tavolicchio.length; j++) {
              if (tavolicchio[j].id === cartePrese1[i].id) {
                tavolicchio.splice(j, 1);
                setTavolo(tavolicchio);
              }
            }
            if (
              cartePrese1[i].valore === 7 &&
              cartePrese1[i].seme === 1 &&
              settebello === 0
            ) {
              //alert("hai il settebello!");
              setTebello(1);
              playSettebello();
            }
            if (cartePrese1[i].valore === 7 && settanta === 0) {
              numeroSette1 = numeroSette1 + 1;
              if (numeroSette1 > 2) {
                //alert("hai la SETTANTA!");
                setTanta(1);
              }
            }
            if (cartePrese1[i].seme === 1 && denari === 0) {
              numeroDenari1 = numeroDenari1 + 1;
              if (numeroDenari1 > 5) {
                //alert("hai I DENARI!");
                setDenari(1);
              }
            }
          }

          setPlayerOne(plone);
          if (plone.length === 0 && playerTwo.length === 0) {
            //alert("player uno ha finito le carte");
            setInMatch(false);
            incartata(player);
            incartata();
          }

          break;
        case 2:
          let numeroSette2 = 0;
          let numeroDenari2 = 0;
          for (let i = 0; i < cartePrese2.length; i++) {
            for (let j = 0; j < playerTwo.length; j++) {
              if (playerTwo[j].id === cartePrese2[i].id) {
                pltwo.splice(j, 1);
              }
            }
            for (let j = 0; j < tavolicchio.length; j++) {
              if (tavolicchio[j].id === cartePrese2[i].id) {
                tavolicchio.splice(j, 1);
                setTavolo(tavolicchio);
              }
            }
            if (
              cartePrese2[i].valore === 7 &&
              cartePrese2[i].seme === 1 &&
              settebello === 0
            ) {
              //alert("hai il settebello!");
              setTebello(2);
              playSettebello();
            }
            if (cartePrese2[i].valore === 7 && settanta === 0) {
              numeroSette2 = numeroSette2 + 1;
              if (numeroSette2 > 2) {
                //alert("hai la SETTANTA!");
                setTanta(2);
              }
            }
            if (cartePrese2[i].seme === 1 && denari === 0) {
              numeroDenari2 = numeroDenari2 + 1;
              if (numeroDenari2 > 5) {
                //alert("hai I DENARI!");
                setDenari(2);
              }
            }
          }

          setPlayerTwo(pltwo);
          if (pltwo.length === 0 && playerOne.length === 0) {
            //alert("player due ha finito le carte");
            setInMatch(false);
            incartata(player);
            incartata();
          }

          break;
        default:
          console.log("liscissimo 1");
      }
      if (tavolicchio.length === 0) {
        switch (player) {
          case 1:
            const temp_scope1 = scope1;
            temp_scope1.push(crd);
            settaScope1(temp_scope1);
            break;
          case 2:
            const temp_scope2 = scope2;
            temp_scope2.push(crd);
            settaScope2(temp_scope2);
            break;
          default:
            console.log("liscissimo 2");
        }

        //alert("SCOPARE");
        playScopa();
      }
    }
  }

  function incartata(player) {
    if (player) {
      setUltimaPresa(player);
    } else {
      /* ALLA FINE PROPRIO */
      let temp_prendiCarte1 = [];
      let temp_prendiCarte2 = [];

      for (let i = 0; i < cartePrese1.length; i++) {
        temp_prendiCarte1.push(cartePrese1[i]);
      }
      for (let i = 0; i < cartePrese2.length; i++) {
        temp_prendiCarte2.push(cartePrese2[i]);
      }

      if (
        cards.length === 0 &&
        playerOne.length === 0 &&
        playerTwo.length === 0
      ) {
        setTavolo([]);
        switch (ultimaPresa) {
          case 1:
            for (let i = 0; i < tavolo.length; i++) {
              temp_prendiCarte1.push(tavolo[i]);
            }
            alert("prende tutto player" + ultimaPresa);
            prendiCarte1(temp_prendiCarte1);
            //finePartita()
            break;
          case 2:
            for (let i = 0; i < tavolo.length; i++) {
              temp_prendiCarte2.push(tavolo[i]);
            }
            alert("prende tutto player" + ultimaPresa);
            prendiCarte2(temp_prendiCarte2);
            //finePartita()
            break;
          default:
            console.log("NON PRENDE NESSUNO");
        }
      }
    }
  }

  function presaAutomatica(temp_tavolo, quindici, temp_crd, callback, player) {
    console.log("PRESA AUTOMATICA");
    const cartaAttuale = temp_crd.valore;
    let temp_tavolo1 = [];
    let temp_tavolo2 = [];
    let temp_tavolo3 = [];
    for (let i = 0; i < temp_tavolo.length; i++) {
      temp_tavolo1.push(temp_tavolo[i]);
    }
    temp_tavolo1.splice(0, 1);
    temp_tavolo1.push(temp_tavolo[0]);

    for (let i = 0; i < temp_tavolo1.length; i++) {
      temp_tavolo2.push(temp_tavolo1[i]);
    }
    temp_tavolo2.splice(0, 1);
    temp_tavolo2.push(temp_tavolo1[0]);

    for (let i = 0; i < temp_tavolo2.length; i++) {
      temp_tavolo3.push(temp_tavolo2[i]);
    }
    temp_tavolo3.splice(0, 1);
    temp_tavolo3.push(temp_tavolo2[0]);

    if (temp_tavolo.length > 0) {
      for (let i = 0; i < temp_tavolo.length; i++) {
        if (temp_tavolo[i].valore + cartaAttuale === quindici) {
          //alert("prendi: " + temp_tavolo[i].valore + "+" + cartaP1);
          somma(temp_tavolo[i], false, player);
          somma(temp_crd, false, player);
          return false;
        } else {
          for (let j = 0; j < temp_tavolo1.length; j++) {
            if (
              temp_tavolo[i].valore + temp_tavolo1[j].valore + cartaAttuale ===
                quindici &&
              temp_tavolo[i].id !== temp_tavolo1[j].id
            ) {
              // 3 CARTE
              somma(temp_tavolo[i], false, player);
              somma(temp_tavolo1[j], false, player);
              somma(temp_crd, false, player);
              return false;
            } else {
              for (let y = 0; y < temp_tavolo2.length; y++) {
                if (
                  temp_tavolo[i].valore +
                    temp_tavolo1[j].valore +
                    temp_tavolo2[y].valore +
                    cartaAttuale ===
                    quindici &&
                  temp_tavolo[i].id !== temp_tavolo1[j].id &&
                  temp_tavolo[i].id !== temp_tavolo2[y].id &&
                  temp_tavolo1[j].id !== temp_tavolo2[y].id &&
                  temp_tavolo1[j].id !== temp_tavolo[j].id &&
                  temp_tavolo2[y].id !== temp_tavolo[i].id &&
                  temp_tavolo2[y].id !== temp_tavolo1[j].id
                ) {
                  somma(temp_tavolo[i], false, player);
                  somma(temp_tavolo1[j], false, player);
                  somma(temp_tavolo2[y], false, player);
                  somma(temp_crd, false, player);
                  return false;
                } else {
                  for (let x = 0; x < temp_tavolo3.length; x++) {
                    if (
                      temp_tavolo[i].valore +
                        temp_tavolo1[j].valore +
                        temp_tavolo2[y].valore +
                        temp_tavolo3[x].valore +
                        cartaAttuale ===
                        quindici &&
                      temp_tavolo[i].id !== temp_tavolo1[j].id &&
                      temp_tavolo[i].id !== temp_tavolo2[y].id &&
                      temp_tavolo[i].id !== temp_tavolo3[x].id &&
                      temp_tavolo1[j].id !== temp_tavolo2[y].id &&
                      temp_tavolo1[j].id !== temp_tavolo3[x].id &&
                      temp_tavolo1[j].id !== temp_tavolo[i].id &&
                      temp_tavolo2[y].id !== temp_tavolo3[x].id &&
                      temp_tavolo2[y].id !== temp_tavolo1[j].id &&
                      temp_tavolo2[y].id !== temp_tavolo[i].id &&
                      temp_tavolo3[x].id !== temp_tavolo[i].id &&
                      temp_tavolo3[x].id !== temp_tavolo1[j].id &&
                      temp_tavolo3[x].id !== temp_tavolo2[y].id
                    ) {
                      somma(temp_tavolo[i], false, player);
                      somma(temp_tavolo1[j], false, player);
                      somma(temp_tavolo2[y], false, player);
                      somma(temp_tavolo3[x], false, player);
                      somma(temp_crd, false, player);

                      return false;
                    }
                  }
                }
              }
            }
          }
        }
      }
      callback();
    } else {
      callback();
    }
  }

  function scarta(crd, player) {
    somma(crd, true, player);
    const temp_tavolo = tavolo;

    presaAutomatica(
      temp_tavolo,
      15,
      crd,
      (callback) => {
        console.log("callback");
        playScarto();
        incartata();
        let temp_tavolo2 = [];
        let temp_player1 = [];
        let temp_player2 = [];
        for (let i = 0; i < temp_tavolo.length; i++) {
          temp_tavolo2.push(temp_tavolo[i]);
        }

        switch (player) {
          case 1:
            for (let i = 0; i < playerOne.length; i++) {
              if (playerOne[i].id !== crd.id) {
                temp_player1.push(playerOne[i]);
              }
            }

            setPlayerOne(temp_player1);
            if (temp_player1.length === 0 && playerTwo.length === 0) {
              setInMatch(false);
              incartata(player);
              incartata();
            }
            break;
          case 2:
            for (let i = 0; i < playerTwo.length; i++) {
              if (playerTwo[i].id !== crd.id) {
                temp_player2.push(playerTwo[i]);
              }
            }

            setPlayerTwo(temp_player2);
            if (temp_player2.length === 0 && playerOne.length === 0) {
              setInMatch(false);
              incartata(player);
              incartata();
            }

            break;
          default:
            console.log("NIENTISSIMO");
        }

        if (crd) {
          temp_tavolo2.push(crd);
        }
        setTavolo(temp_tavolo2);
      },
      player
    );
  }

  function onDragEnd(result) {
    let draggableId = 0;
    let draggableInx = 0;

    if (result.draggableId) {
      draggableId = Number(result.draggableId);
    }
    if (result.source) {
      draggableInx = result.source.index;
    }
    console.log(result);
    // dropped outside the list
    if (!result.destination) {
      return;
    }
    const insert = (arr, index, newItem) => [
      // part of the array before the specified index
      ...arr.slice(0, index),
      // inserted item
      newItem,
      // part of the array after the specified index
      ...arr.slice(index),
    ];

    switch (result.source.droppableId) {
      case "plone":
        if (result.destination.droppableId === "table") {
          console.log("AQQUAINE");
          presaAutomatica(
            tavolo,
            15,
            playerOne[draggableInx],
            () => {
              playScarto()
              console.log("nel tavolo");

              let temp_tavolo = [];
              let temp_player = [];

              for (let i = 0; i < tavolo.length; i++) {
                temp_tavolo.push(tavolo[i]);
              }
              for (let i = 0; i < playerOne.length; i++) {
                if (playerOne[i].id !== draggableId) {
                  temp_player.push(playerOne[i]);
                }
              }

              let temp_tavoloZ = insert(
                temp_tavolo,
                result.destination.index,
                playerOne[draggableInx]
              );

              let items2 = [];
              if (temp_player.length > 0) {
                items2 = reorder(
                  temp_player
                  //result.source.index,
                  //result.destination.index
                );
              }

              console.log(temp_tavoloZ, items2);

              setTavolo(temp_tavoloZ);
              setPlayerOne(items2);
              if (items2.length === 0 && playerOne.length === 0) {
                //alert("player due ha finito le carte");
                setInMatch(false);
                incartata(1);
                incartata();
              }
            },
            1
          );
        } else {
          console.log("tra di loro");

          const items1 = reorder(
            playerOne,
            result.source.index,
            result.destination.index
          );

          setPlayerOne(items1);
        }

        break;
      case "pltwo":
        if (result.destination.droppableId === "table") {
          console.log("AQQUAINE");
          presaAutomatica(
            tavolo,
            15,
            playerTwo[draggableInx],
            () => {
              playScarto()
              console.log("nel tavolo");

              let temp_tavolo = [];
              let temp_player = [];

              for (let i = 0; i < tavolo.length; i++) {
                temp_tavolo.push(tavolo[i]);
              }
              for (let i = 0; i < playerTwo.length; i++) {
                if (playerTwo[i].id !== draggableId) {
                  temp_player.push(playerTwo[i]);
                }
              }

              const temp_tavoloZ = insert(
                temp_tavolo,
                result.destination.index,
                playerTwo[draggableInx]
              );

              let items2 = [];
              if (temp_player.length > 0) {
                items2 = reorder(
                  temp_player
                  //result.source.index,
                  //result.destination.index
                );
              }

              console.log(temp_tavoloZ, items2);

              setTavolo(temp_tavoloZ);
              setPlayerTwo(items2);
              if (items2.length === 0 && playerOne.length === 0) {
                //alert("player due ha finito le carte");
                setInMatch(false);
                incartata(2);
                incartata();
              }
            },
            2
          );
        } else {
          console.log("tra di loro");

          const items1 = reorder(
            playerTwo,
            result.source.index,
            result.destination.index
          );

          setPlayerTwo(items1);
        }
        break;
      default:
        if (playerTwo.length === 0 && playerOne.length === 0) {
          //alert("player due ha finito le carte");
          setInMatch(false);
          incartata();
        }

        console.log("UN CAZZO!");
    }
  }

  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <div className="tavoloContainer">
        {cards.length > 0 ? (
          <div
            style={{ backgroundImage: "url(" + retro + ")" }}
            className="mazzo"
            onClick={() => daiLeCarte()}
          >
            <span className="numeroMazzo">{cards.length}</span>
          </div>
        ) : playerOne.length === 0 && playerTwo.length === 0 ? (
          <div className="mazzo fineMazzo" onClick={() => finePartita()}>
            <span>RISULTATI</span>
          </div>
        ) : (
          <div className="mazzo fineMazzo"></div>
        )}
        {cartePrese1.length > 0 ? (
          <div
            style={{ backgroundImage: "url(" + retro + ")" }}
            className="cartePrese preseOne"
          >
            {settebello === 1 && <span className="settebello">S</span>}
            <span className="numeroMazzo">{cartePrese1.length}</span>
          </div>
        ) : (
          <div className="cartePrese preseOne fineMazzo"></div>
        )}
        {cartePrese2.length > 0 ? (
          <div
            style={{ backgroundImage: "url(" + retro + ")" }}
            className="cartePrese preseTwo"
          >
            {settebello === 2 && <span className="settebello">S</span>}
            <span className="numeroMazzo">{cartePrese2.length}</span>
          </div>
        ) : (
          <div className="cartePrese preseTwo fineMazzo"></div>
        )}

        <div className="cartePrese scopeTwo preseTwo">
          {scope2.length !== 0 && (
            <Card scopa={true} card={scope2[scope2.length - 1]} />
          )}
          {scope2.length !== 0 && (
            <span className="numeroMazzo">{scope2.length}</span>
          )}
        </div>
        <div className="cartePrese scopeOne preseOne">
          {scope1.length !== 0 && (
            <Card scopa={true} card={scope1[scope1.length - 1]} />
          )}
          {scope1.length !== 0 && (
            <span className="numeroMazzo">{scope1.length}</span>
          )}
        </div>
        <Droppable droppableId="plone" type="card" direction="horizontal">
          {(provided, snapshot) => (
            <div
              ref={provided.innerRef}
              style={getListStyle2(snapshot.isDraggingOver)}
              {...provided.droppableProps}
            >
              {playerOne.map((card, index) => {
                return (
                  <Draggable
                    key={card.id}
                    draggableId={String(card.id)}
                    index={index}
                  >
                    {(provided, snapshot) => (
                      <div
                        ref={provided.innerRef}
                        {...provided.draggableProps}
                        {...provided.dragHandleProps}
                        style={getItemStyle2(
                          snapshot.isDragging,
                          provided.draggableProps.style
                        )}
                      >
                        <Card
                          index={index}
                          attivazione={(crd, deseleziona, player) =>
                            somma(crd, deseleziona, player)
                          }
                          /* hidden={true} */
                          card={card}
                          key={index}
                          scarta={(crd, player) => scarta(crd, player)}
                          player={1}
                        />
                      </div>
                    )}
                  </Draggable>
                );
              })}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
        <div className="tavolo">
          <Droppable droppableId="table" type="card" direction="horizontal">
            {(provided, snapshot) => (
              <div
                ref={provided.innerRef}
                style={getListStyle3(snapshot.isDraggingOver)}
                {...provided.droppableProps}
              >
                {tavolo.map((card, index) => {
                  return (
                    <Draggable
                      key={card.id}
                      draggableId={card.id + ""}
                      index={index}
                      isDragDisabled={true}
                    >
                      {(provided, snapshot) => (
                        <div
                          ref={provided.innerRef}
                          {...provided.draggableProps}
                          {...provided.dragHandleProps}
                          style={getItemStyle3(
                            snapshot.isDragging,
                            provided.draggableProps.style
                          )}
                        >
                          <Card
                            index={index}
                            attivazione={(crd, deseleziona) =>
                              somma(crd, deseleziona)
                            }
                            table={true}
                            card={card}
                            key={index}
                          />
                        </div>
                      )}
                    </Draggable>
                  );
                })}
                {provided.placeholder}
              </div>
            )}
          </Droppable>
        </div>

        <Droppable droppableId="pltwo" type="card" direction="horizontal">
          {(provided, snapshot) => (
            <div
              ref={provided.innerRef}
              style={getListStyle(snapshot.isDraggingOver)}
              {...provided.droppableProps}
            >
              {playerTwo.map((card, index) => {
                return (
                  <Draggable
                    key={card.id}
                    draggableId={String(card.id)}
                    index={index}
                  >
                    {(provided, snapshot) => (
                      <div
                        ref={provided.innerRef}
                        {...provided.draggableProps}
                        {...provided.dragHandleProps}
                        style={getItemStyle(
                          snapshot.isDragging,
                          provided.draggableProps.style
                        )}
                      >
                        <Card
                          index={index}
                          attivazione={(crd, deseleziona, player) =>
                            somma(crd, deseleziona, player)
                          }
                          /* hidden={true} */
                          card={card}
                          key={index}
                          scarta={(crd, player) => scarta(crd, player)}
                          player={2}
                        />
                      </div>
                    )}
                  </Draggable>
                );
              })}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
      </div>
    </DragDropContext>
  );
}

export default Tavolo;
