function mescola(array) {
    let currentIndex = array.length,
      randomIndex;

    // While there remain elements to shuffle.
    while (currentIndex !== 0) {
      // Pick a remaining element.
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex--;

      // And swap it with the current element.
      [array[currentIndex], array[randomIndex]] = [
        array[randomIndex],
        array[currentIndex],
      ];
    }

    return array;
  }
  function prendiUnNumeroACaso(min, max) {
    return Math.floor(Math.random() * (max - min) ) + min;
  }
  function mischia (arr) {
      const primaMescolata = mescola(arr)
      const secondaMescolata = mescola(primaMescolata)
      const terzaMescolata = mescola(secondaMescolata)

       const random = prendiUnNumeroACaso(11, -11)

       //console.log(random)

      const meta = Math.ceil(terzaMescolata.lenght / 2);
      let mazzo1 = []
      let mazzo2 = []
      for(let i = 0; i<terzaMescolata.length; i++) {
          if(i < meta + random) {
              mazzo1.push(terzaMescolata[i])
          } else {
              mazzo2.push(terzaMescolata[i])
          }
      }
      const mazzo = mazzo2.concat(mazzo1);

      //console.log(mazzo)
      return mazzo

  }

  export { mischia }