import React, { useEffect, useState } from "react";
import { retro } from "./cards";

function Card(props) {
  const card = props.card;
  const [attivo, setAttivo] = useState(false);

  function attivaLaCarta(crd) {
    //setAttivo(!attivo);
    //console.log("CARAMELLA")
    //props.attivazione(crd, attivo, props.player);
  }
  function scarta(crd) {
    setAttivo(false);
    //console.log(crd)
    props.scarta(crd, props.player);
  }

  return (
    <div style={{ position: "relative" }}>
      {attivo && !props.hidden && !props.table && (
        <button
          onClick={() => scarta(card)}
          className={
            props.player === 1 ? "scartaOne scartaBtn" : "scartaTwo scartaBtn"
          }
        >
          SCARTA
        </button>
      )}
      {props.hidden ? (
        <div className="card hidden">
          <img width={"100%"} src={retro} alt="retro" />
        </div>
      ) : props.table ? (
        <div
          className={"card" + (attivo ? " attiva" : " attive")}
          onClick={() => {
            attivaLaCarta(card);
          }}
        >
          <img width={"100%"} src={card.img} alt="carta" />
        </div>
      ) : props.scopa ? (
        <div className={"card " + (attivo ? " attiva" : "")}>
          <img width={"100%"} src={card.img} alt="carta" />
        </div>
      ) : (
        <div
          className={"card " + (attivo ? " attiva" : " attive")}
          onClick={() => {
            attivaLaCarta(card);
          }}
        >
          <img width={"100%"} src={card.img} alt="carta" />
        </div>
      )}
    </div>
  );
}

export default Card;
