import logo from "./logo.svg";
import "./App.css";
import Home from "./Home/Home";
import { BrowserRouter, Route } from "react-router-dom";
import Tavolo from "./Tavolo/Tavolo";
import { Switch } from "react-router-dom";

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/tavolo">
            <Tavolo />
          </Route>
        </Switch>
      </BrowserRouter>
    </div>
  );
}

export default App;
